<template>
  <div class="header">
    <div class="headerLeft">
      <h1 class="">应急演练系统</h1>
    </div>
    <div class="headerMiddle clearfix">
      <ul class="clearfix">
        <li @click="headerClick(1)">
          <span :class="$store.state.showHeader == '1' ? 'is_headActive': ''">首页</span>
        </li>
        <li @click="headerClick(2)" >
          <span :class="$store.state.showHeader == '2' ? 'is_headActive': ''">新建演练项目</span>
        </li>
        <li @click="headerClick(3)" >
          <span :class="$store.state.showHeader == '3' ? 'is_headActive': ''">可参加的演练</span>
        </li>
        <li @click="headerClick(4)" >
          <span :class="$store.state.showHeader == '4' ? 'is_headActive': ''">历史演练项目</span>
        </li>
      </ul>
    </div>
    <div class="headerRight">
      <div class="user">
        <span>欢迎您！{{ name }}</span><i></i>
      </div>
      <div class="set">
        <p>
          <i @click="headerClick(5)" :class="$store.state.showHeader == '5' ? 'is_headActive': ''">设置</i>
          <span>|</span>
          <i @click="headerClick(6)" :class="$store.state.showHeader == '6' ? 'is_headActive': ''">退出</i>
        </p>
      </div>
    </div>
  </div>
</template>
<script>
  export default{
    data(){
      return{
          name: ''
      }
    },
    created(){
//      console.log(JSON.parse(localStorage.user))
      this.name = JSON.parse(localStorage.user).name;
    },
    methods: {
      headerClick(val){
        this.$store.state.showHeader = val;
        if(val== '1'){
          this.$router.push('/')
        } else if(val == '2'){
          this.$router.push('/newhome')
        } else if(val =='3'){
          this.$store.state.status = false;
          this.$router.push('/enterable')
        } else if(val == '4'){
          this.$router.push('/HistoricalRehearsal/2')
        } else if(val=='5'){
          this.$router.push('/set')
        }else if(val == '6'){
          this.$router.push('/Login');
          localStorage.clear();
        }else if(val == '7'){
//          this.$router.push('/Reference')
        }
      }
    }
  }
</script>
<style lang="less" rel="stylesheet/less">
  @import '../../../static/css/base.css';
  .header{
    width: 100%;
    min-width: 1280px;
    height: 80px;
    background-color:#50b4ec;
    .headerLeft{
      margin-left: 40px;
      margin-right: 50px;
      float: left;
      h1{
        line-height: 80px;
        font-family: Arial;
        font-size: 30px;
        font-weight: 900;
        color: #ffffff;
      }
    }
    .headerMiddle{
      float: left;
      ul{
          li{
              float: left;
              line-height: 80px;
              display: inline-block;
              margin-right: 40px;
              font-family: PingFangSC;
              font-size: 20px;
              color: #ffffff;
              cursor: pointer;

              height: 80px;
              span{
                  display: inline-block;
                  height: 57px;
                  margin-top: 23px;
                  padding: 0 30px;
                  line-height: 40px;
              }
          }
        .is_headActive{
          border-radius:8px 8px 0 0;
          background-color: #ffffff;
          color: #4a4a4a;
        }
      }
    }
    .headerRight{
      float: right;
      margin:10px 60px 0 0;
      .user{
        span{
          font-family: PingFangSC;
          font-size: 16px;
          color: #ffffff;
        }
      }
      .set{
        padding: 6px 12px 8px 21px;
        span{
          color: #ffffff;
        }
        i{
          display:inline-block;
          font-family: PingFangSC;
          font-size: 16px;
          color: #ffffff;
          cursor: pointer;

        }
      }
    }
  }
</style>



